/**
 * 
 * @param countTime 
 * @param basicMs   
 * @param progressFn  
 * @param callBackFn  
 */
const countFn = (countTime, basicMs, progressFn, callBackFn) => {
  for (let i = 0; i < countTime; ++i) {
    ((j = i) => {
      setTimeout(() => {
        if (i === 0) {
          console.log("count start!");
        }
        if (i == countTime - 1) {
          console.log("count done!");
          callBackFn();
        } else {
          progressFn();
        }
      }, basicMs * j);
    })(i);
  }
};

/**
 *
 * @param msTime 
 * @returns {{hour: string, minute: string}}
 */
const parseTime = msTime => {
  let _tempDate = new Date(msTime);
  return {
    year: _tempDate.getFullYear().toString(),
    month:
      _tempDate.getMonth() + 1 > 9
        ? (_tempDate.getMonth() + 1).toString()
        : "0" + (_tempDate.getMonth() + 1).toString(),
    date:
      _tempDate.getDate() > 9
        ? _tempDate.getDate().toString()
        : "0" + _tempDate.getDate().toString(),
    hour:
      _tempDate.getHours() > 9
        ? _tempDate.getHours().toString()
        : "0" + _tempDate.getHours().toString(),
    minute:
      _tempDate.getMinutes() > 9
        ? _tempDate.getMinutes().toString()
        : "0" + _tempDate.getMinutes().toString()
  };
};

/**
 * /
 * @param msTime 
 * @returns {number} -2:  -1  2 1  0 
 */
const judgeTime = msTime => {
  let _nowDate = new Date();
  let _msTime = new Date(msTime);
  let _y = _nowDate.getFullYear();
  let _m = _nowDate.getMonth() + 1;
  let _d = _nowDate.getDate();
  if (_y === _msTime.getFullYear() && _m === _msTime.getMonth() + 1) {
    if (_d - _msTime.getDate() === 0) {
      return 0; // 
    }
    if (_d - _msTime.getDate() === -1) {
      return 1; // 
    }
    if (_d - _msTime.getDate() > 0) {
      return 2; // 
    } else {
      return -1; // 
    }
  } else {
    return -2; //  /
  }
};

/**
 * randomNum 
 * @param fromNum 
 * @param toNum  
 * @returns {Number} 
 */
const randomNum = (fromNum, toNum) => {
  let _a = Math.random() * toNum;
  while (_a < fromNum || _a > toNum) {
    _a = Math.random() * toNum;
  }
  if (_a > toNum - 0.5) {
    _a = toNum;
  }
  return parseInt(_a);
};

/**
 * 
 * @param values 
 * @returns {number} 0 1
 */
const verifyVal = (...values) => {
  // console.log(values)
  for (let i of values) {
    if (i == "" || i == null || i == undefined) {
      return 1;
    }
  }
  return 0;
};

const monthFilter = month => {
  switch (month) {
    case "01":
      return "January";
      break;
    case "02":
      return "February";
      break;
    case "03":
      return "March";
      break;
    case "04":
      return "April";
      break;
    case "05":
      return "May";
      break;
    case "06":
      return "June";
      break;
    case "07":
      return "July";
      break;
    case "08":
      return "August";
      break;
    case "09":
      return "September";
      break;
    case "10":
      return "October";
      break;
    case "11":
      return "November";
      break;
    case "12":
      return "December";
      break;
  }
};

export { countFn, parseTime, judgeTime, randomNum, verifyVal, monthFilter };
